import React, { useEffect, memo, useMemo, useCallback, useState, useRef } from 'react'
import cn from 'classnames'

import { useOnClickOutside } from 'src/hooks'

import { ReactComponent as BellIcon } from 'src/assets/images/icons/bell.svg'
import { ReactComponent as ErrorIcon } from 'src/assets/images/icons/notification_error.svg'
import { ReactComponent as InfoIcon } from 'src/assets/images/icons/notification_info.svg'
import { ReactComponent as SuccessIcon } from 'src/assets/images/icons/notification_success.svg'
import { ReactComponent as WarningIcon } from 'src/assets/images/icons/notification_warning.svg'
import { ReactComponent as RemoveIcon } from 'src/assets/images/icons/notification_remove.svg'

import { useMappedActions, useMappedState } from './bindings'
import styles from './styles.module.scss'

const NotificationsRaw = () => {
  const actions = useMappedActions()
  const { notifications } = useMappedState()
  const [isOpened, setIsOpened] = useState(false)
  const notificationsRef = useRef(null)

  const hasNotification = !!notifications.length

  useOnClickOutside(notificationsRef, () => {
    setIsOpened(false)
  })

  useEffect(() => {
    actions.loadNotifications()
  }, [actions])

  const onClickHead = useCallback(() => {
    setIsOpened(!isOpened)
  }, [isOpened, setIsOpened])

  const renderEmpty = useMemo(() => {
    return (
      <div className={styles.empty}>
        <div className={styles.emptyText}>You have no new notifications</div>
      </div>
    )
  }, [])

  const getIcon = useCallback((type) => {
    switch (type) {
      case 'info': {
        return <InfoIcon />
      }
      case 'error': {
        return <ErrorIcon />
      }
      case 'success': {
        return <SuccessIcon />
      }
      case 'warning': {
        return <WarningIcon />
      }
      default: {
        return null
      }
    }
  }, [])

  const renderItems = useMemo(() => {
    return notifications.map((notification) => {
      const { type, title, message, id } = notification
      return (
        <div className={styles.item}>
          <div className={cn(styles.itemIcon, styles[`itemIcon${type.toUpperCase()}`])}>{getIcon(type)}</div>
          <div className={styles.itemContent}>
            {title && <div className={styles.itemTitle}>{title}</div>}
            <div className={styles.itemMessage}>{message}</div>
          </div>
          <div className={styles.itemAction}>
            <button className={styles.itemRemove} onClick={() => actions.removeNotifications(id)}>
              <RemoveIcon />
            </button>
          </div>
        </div>
      )
    })
  }, [notifications, actions, getIcon])

  return (
    <div className={styles.root} ref={notificationsRef}>
      <div
        className={cn(styles.head, {
          [styles.headActive]: isOpened,
        })}
        onClick={onClickHead}
      >
        <BellIcon className={styles.icon} />
        {hasNotification && <div className={styles.mark}></div>}
      </div>
      {isOpened && (
        <div className={styles.body}>
          <div className={styles.top}>
            <div className={styles.title}>Notification</div>
            <button className={styles.readAll} onClick={actions.removeAllNotifications}>
              Read all ({notifications.length})
            </button>
          </div>
          <div className={styles.middle}>{hasNotification ? renderItems : renderEmpty}</div>
        </div>
      )}
    </div>
  )
}

export const Notifications = memo(NotificationsRaw)
export default Notifications
