import React, { memo } from 'react'
import { NavLink } from 'react-router-dom'
import cn from 'classnames'

import { ReactComponent as BackIcon } from 'src/assets/images/icons/back.svg'

import styles from './styles.module.scss'
import Profile from './components/Profile'
import Notifications from './components/Notifications'

const HeaderRaw = ({ backRoute, pageTitle }) => {
  const hasLeft = backRoute || pageTitle
  return (
    <header className={styles.header}>
      {hasLeft && (
        <div className={styles.left}>
          {backRoute && (
            <div className={styles.back}>
              <NavLink to={backRoute} className={styles.backLink}>
                <BackIcon className={styles.backIcon} />
                <span className={styles.backLabel}>Back</span>
              </NavLink>
            </div>
          )}
          {pageTitle && <div className={styles.title}>{pageTitle}</div>}
        </div>
      )}
      <div
        className={cn(styles.right, {
          [styles.rightPadding]: !!pageTitle,
        })}
      >
        <div className={styles.notifications}>
          <Notifications />
        </div>
        <div className={styles.profile}>
          <Profile />
        </div>
      </div>
    </header>
  )
}

export const Header = memo(HeaderRaw)
export default Header
