import { useMapState, useMapActions } from 'src/hooks'
import { notificationsThunks } from 'src/thunks'
import { notificationsSelectors } from 'src/selectors'

export const useMappedState = () => {
  return useMapState((state) => {
    return {
      notifications: notificationsSelectors.getNotifications(state),
    }
  })
}

export const useMappedActions = () => {
  return useMapActions({
    loadNotifications: notificationsThunks.loadNotifications,
    removeNotifications: notificationsThunks.removeNotifications,
    removeAllNotifications: notificationsThunks.removeAllNotifications,
  })
}
